* {
  box-sizing: border-box;
}

img {
  width: 100%;
  max-width: 100%;
}

body {
  font-size: 1em;
  font-family: "Quicksand", sans-serif;
  line-height: 1.5;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Roboto Slab", serif;
  color: #99DDCC;
}

a {
  color: #99DDCC;
}

.header {
  background-color: #99DDCC;
}

.logo {
  width: 15rem;
}

.nav {
  width: auto;
  position: absolute;
  bottom: 0;
  right: 0;
}

.nav-link {
  display: none;
  font-size: 1.5rem;
  font-family: "Quicksand", sans-serif;
  color: #fff;
  text-decoration: none;
  margin: 1.5rem 0;
}
.nav-link:hover {
  color: #8ADB55;
}
@media (min-width: 768px) {
  .nav-link {
    display: block;
    padding: 0.5rem 1.5rem;
    font-size: 1rem;
  }
}

.hamburger {
  cursor: pointer;
  color: #fff;
  font-size: 2.5rem;
  height: 100%;
  align-items: center;
  justify-content: center;
  padding-right: 1rem;
  position: relative;
}
@media (min-width: 768px) {
  .hamburger {
    display: none;
  }
}

.close-btn {
  cursor: pointer;
  color: #fff;
  font-size: 3rem;
  position: absolute;
  top: 0;
  right: 0;
}
@media (min-width: 768px) {
  .close-btn {
    display: none;
  }
}

.nav-menu {
  height: 100%;
  display: flex;
}

.nav-menu-toggle {
  background-color: #99DDCC;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100vw;
  height: 900px;
  z-index: 99999;
  animation: growDown 300ms ease-in-out forwards;
  transform-origin: top center;
  position: relative;
  left: 0;
  top: 820px;
}
@keyframes growDown {
  0% {
    transform: scaleY(0);
  }
  80% {
    transform: scaleY(1.1);
  }
  100% {
    transform: scaleY(1);
  }
}
.nav-menu-toggle .nav-link {
  display: block;
}
@media (min-width: 768px) {
  .nav-menu-toggle {
    display: none;
  }
}

.container {
  position: relative;
}
@media (min-width: 1480px) {
  .container {
    width: 1400px;
    margin: 0 auto;
  }
}

.column-6 {
  width: 100%;
}
@media (min-width: 768px) {
  .column-6 {
    width: 50%;
  }
}
.column-6__img {
  position: fixed;
  margin-top: 20px;
  margin-left: 80px;
  margin-right: none;
  width: 30%;
  height: 40%;
}

.column-6.garden-6 {
  padding: 3rem;
  text-align: left;
}
@media (min-width: 768px) {
  .column-6.garden-6 {
    width: 75%;
  }
}

.column-9 {
  width: 100%;
}
@media (min-width: 768px) {
  .column-9 {
    width: 75%;
  }
}

.column-9.garden {
  width: 100%;
  padding: 1rem;
  text-align: left;
}
.column-9.garden h1,
.column-9.garden h2,
.column-9.garden h3,
.column-9.garden h4,
.column-9.garden h5,
.column-9.garden h6 {
  width: 100%;
  text-align: left;
  color: #E5890A;
}
.column-9.garden p {
  text-align: left;
}
@media (min-width: 768px) {
  .column-9.garden {
    width: 100%;
  }
}

.flex-container, .home-flex {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .flex-container, .home-flex {
    flex-direction: row;
  }
}

.flex-container.flex-garden, .flex-garden.home-flex {
  justify-content: space-between;
  width: 100%;
}

.flex-column {
  flex-direction: column;
}
.flex-column-reverse {
  flex-direction: column-reverse;
}

.flex-row {
  flex-direction: row;
}
.flex-row-reverse {
  flex-direction: row-reverse;
}

.centre-flex {
  justify-content: center;
}

.margin-container {
  margin: 1.5rem;
}

.home-flex {
  height: 75vh;
  align-items: center;
}

.form-box, .description-box {
  position: relative;
  width: 75%;
  height: 2rem;
  background: #ffffff;
  border: 0.1rem solid #B8DFD8;
  border-radius: 0.4rem;
}
@media (min-width: 768px) {
  .form-box, .description-box {
    width: 100%;
  }
}

.form-title {
  text-align: center;
}

.description-box {
  height: 8rem;
}

.label {
  position: relative;
  line-height: 2rem;
  color: #363636;
  display: block;
  margin: 0 auto;
}

.form-input {
  height: 1.75rem;
  width: 75%;
  font-family: "Quicksand", sans-serif;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .form-input {
    width: 100%;
  }
}

.form-textarea {
  height: 8rem;
  width: 75%;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .form-textarea {
    width: 100%;
  }
}

.button-group {
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;
  text-align: center;
}
.button-group button {
  margin: 0.5rem;
  align-content: center;
  align-items: center;
  min-width: 8rem;
  text-align: center;
}
@media (min-width: 768px) {
  .button-group button {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
    flex-wrap: wrap;
    text-align: center;
    margin: 0.5rem;
  }
}

.form-content {
  margin-left: 1rem;
  margin-right: 1rem;
}

.button-primary, .button-secondary, .submit, .profile-submit {
  background-color: #E5890A;
  border: none;
  color: white;
  padding: 1rem;
  text-align: center;
  display: block;
  text-decoration: none;
  border-radius: 4px;
}
.button-primary:hover, .button-secondary:hover, .submit:hover, .profile-submit:hover {
  background-color: #99DDCC;
  border: none;
  color: white;
}

.submit, .profile-submit {
  display: flex;
  width: auto;
  margin: 5px auto;
  align-items: center;
}

.profile-submit {
  width: 150px;
  margin-top: 25px;
  padding: 10px;
  justify-content: center;
}

.button-width {
  width: 100%;
}
@media (min-width: 1020px) {
  .button-width {
    width: 50%;
  }
}

.button-secondary {
  background-color: #E5890A;
  border-radius: 0.25rem;
  width: 9rem;
}

.inline-button {
  display: inline-block;
}

.register-button {
  background-color: #E5890A;
  margin-left: 14px;
  color: white;
  border-radius: 4px;
}
.register-button:hover {
  background-color: #172450;
  color: white;
}

.card-container {
  width: 83.3333333333%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.card-header a {
  color: #172450;
}

.card {
  display: flex;
  flex-direction: column;
  position: relative;
  max-width: 33.3333333333%;
  margin: 1rem 2rem;
  padding: 3rem;
  background-color: rgba(211, 230, 216, 0.4);
  box-shadow: 0px 10px 40px 0px rgba(0, 0, 0, 0.35);
  border-radius: 0.8rem;
  color: #172450;
}

.page__link {
  overflow-wrap: break-word;
  word-wrap: break-word;
  -ms-word-break: break-all;
  word-break: break-all;
  word-break: break-word;
  -ms-hyphens: auto;
  -moz-hyphens: auto;
  -webkit-hyphens: auto;
  hyphens: auto;
}

.title-link {
  color: #363636;
}
.title-link:hover {
  color: #8ADB55;
}

.list-primary {
  padding: 0;
}
.list-primary li {
  display: block;
  margin-bottom: 0.5rem;
}

.waitIndicator {
  position: absolute;
  top: 1rem;
  left: 12rem;
}
.waitIndicator--img {
  width: 3.4em;
  padding: 0.2em;
  -webkit-animation: rotation 1.8s infinite linear;
  -moz-animation: rotation 1.8s infinite linear;
  animation: rotation 1.8s infinite linear;
}

@keyframes rotation {
  0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  25% {
    -webkit-transform: rotate(40deg);
    -moz-transform: rotate(40deg);
    transform: rotate(40deg);
  }
  50% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  75% {
    -webkit-transform: rotate(-40deg);
    -moz-transform: rotate(-40deg);
    transform: rotate(-40deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    transform: rotate(0deg);
  }
}
.map {
  border-radius: 50%;
  margin: 3rem auto;
  padding: 50%;
  border: solid #E5890A;
}
@media (min-width: 768px) {
  .map {
    border-radius: 1rem;
    width: 100%;
    object-fit: contain;
  }
}

.graph-map {
  padding: 3rem;
  width: 90%;
  width: 100%;
  text-align: left;
}
@media (min-width: 768px) {
  .graph-map {
    width: 50%;
    padding: 10px 10px;
    margin-left: 0;
  }
}
.graph-map .svg {
  padding: 1rem;
  display: block;
  position: relative;
  width: 100%;
  justify-content: space-around;
  align-items: stretch;
  align-content: stretch;
}
.graph-map .svg::after {
  position: relative;
  display: block;
  margin: 0;
  object-fit: contain;
}

.scroll {
  overflow-y: scroll;
  max-height: 500px;
}

.alert-container {
  background-color: none;
  margin: 0 auto;
  height: 10vh;
  width: 30vh;
  position: absolute;
  align-items: center;
  left: 0;
  right: 0;
  margin-top: 300px;
  margin-left: auto;
  margin-right: auto;
  display: block;
  z-index: 9999;
}

.alert-msg {
  display: block;
  background-color: #f9d8d8;
  border: 1px solid #D11D05;
  font-size: 1rem;
  justify-content: center;
  border-radius: 5px;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: inherit;
  top: 20px;
  color: #D11D05;
  padding: 1rem;
  width: inherit;
}

.alert-close {
  color: #D11D05;
  position: absolute;
  z-index: 999;
  right: 0;
}

.inputError {
  color: #D11D05;
}

.event-container {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.add-event {
  margin-top: 1rem;
  text-align: center;
}
.add-event a {
  text-transform: none;
}

.events-list {
  padding: 2rem;
  border: solid #B8DFD8;
  border-radius: 1rem;
  text-align: left;
  width: 100%;
}
@media (min-width: 768px) {
  .events-list {
    width: 100%;
  }
}

.card-title {
  text-align: left;
}

.events-title {
  color: #E5890A;
  text-align: left;
  padding-left: 1rem;
}
.events-title .events-titleh1 {
  margin-block-end: 0.3em;
}

.collapsible__content-inner {
  text-align: left;
  width: 100%;
}

.card-primary {
  text-align: left;
}
.card-primary .list-primary li {
  text-align: left;
}

.hero-card {
  background-color: rgba(211, 230, 216, 0.4);
  box-shadow: 0px 10px 40px 0px rgba(0, 0, 0, 0.35);
  z-index: 9999;
  border-radius: 1.2rem;
  max-width: 50%;
  padding: 3rem;
}

.hero-image {
  position: absolute;
  right: 0;
  top: 0;
  z-index: -1;
  max-width: 66.6666666667%;
  margin: 4rem 0;
  border-radius: 1.2rem;
  box-shadow: 0px 10px 40px 0px rgba(0, 0, 0, 0.35);
}

.hero-title {
  color: #172450;
  font-size: 3rem;
  margin: 0;
}
.hero-title span {
  color: #99DDCC;
  margin: 0;
  width: 100%;
  display: block;
}

.hero-tagline {
  font-size: 1.2rem;
}
@media (min-width: 1240px) {
  .hero-tagline {
    font-size: 1.5rem;
  }
}